import * as ejs from 'ejs'
import * as fse from 'fs-extra'
import * as path from 'path'
import * as glob from 'glob'
import * as prettier from 'prettier'

export type YapiInfo = {
  query_path: { path: string }
  method: string
  title: string
  project_id: number
  req_params: {
    name: string
    desc: string
  }[]
  _id: number
  req_query: { required: '0' | '1'; name: string }[]
  res_body_type: 'raw' | 'json'
  res_body: string
  username: string
}

export type Model = {
  type: string
  requestBodyType?: string
  funcName: string
  typeName: string
  inputValues: string[]
  api?: YapiInfo
  mockCode: string
  mockData: string
  jsonData: any
  jsonKeys?: string[]
  rawSelectedText: string // 
  rawClipboardText: string // 
  activeTextEditorFilePath?: string // 
  createBlockPath?: string // 
}

export const compile = (templateString: string, model: Model) => ejs.render(templateString, model)

export async function renderEjsTemplates(
  templateData: object,
  templateDir: string,
  exclude: string[] = []
) {
  console.log('renderEjsTemplates', templateData)
  console.log('renderEjsTemplates2', templateDir)
  console.log('renderEjsTemplates3', exclude)
  return new Promise<void>((resolve, reject) => {
    glob(
      '**',
      {
        cwd: templateDir,
        ignore: ['node_modules/**'],
        nodir: true,
        dot: true,
      },
      (err, files) => {
        console.log(err, files, 'err-glob')
        if (err) {
          return reject(err)
        }
        const templateFiles = files.filter(s => {
          let valid = true
          if (s.indexOf('.ejs') < 0) {
            valid = false
          }
          if (exclude && exclude.length > 0) {
            exclude.map(e => {
              if (s.startsWith(e)) {
                valid = false
              }
            })
          }
          return valid
        })
        console.log(templateFiles, 'templateFilestemplateFiles')
        Promise.all(
          templateFiles.map(file => {
            const filepath = path.join(templateDir, file)
            console.log('alexalex')
            return renderFile(filepath, templateData)
          })
        )
          .then(() => resolve())
          .catch(reject)
      }
    )
  })
}

async function renderFile(templateFilepath: string, data: ejs.Data) {
  console.log('renderFile', templateFilepath, data)
  let content = await ejs.renderFile(templateFilepath, data)
  console.log(content, 'content-renderfile')
  const targetFilePath = templateFilepath
    .replace(/\.ejs$/, '')
    .replace(/\$\{.+?\}/gi, match => data[match.replace(/\$|\{|\}/g, '')] || '')
  try {
    content = prettier.format(content, {
      singleQuote: true,
      filepath: targetFilePath,
    })
  } catch {}
  await fse.rename(templateFilepath, targetFilePath)
  console.log(content, 'content')
  console.log(targetFilePath, 'targetFilePath')
  await fse.writeFile(targetFilePath, content)
}
