import { Context, Next } from 'koa';

const Koa = require('koa');
const Router = require('koa-router');
const bodyParser = require('koa-bodyparser');
const cors = require('@koa/cors');

interface FormItem {
  name?: string;
  type?: string;
  optional?: boolean;
  defaultValue?: string;
  component?: string;
  required?: boolean;
  showMore?: boolean;
  slots?: boolean;
  key?: string;
  placeholder?: string;
  label?: string;
  propType?: string;
  size?: string;
  border?: boolean;
  disabled?: boolean;
  readonly?: boolean;
  colon?: boolean;
  center?: boolean;
  clearable?: boolean;
  clickable?: boolean;
  isLink?: boolean;
  autofocus?: boolean;
  showWordLimit?: boolean;
  arrowDirection?: string;
  labelAlign?: string;
  inputAlign?: string;
  maxlength?: string;
  labelSlot?: boolean;
  leftIcon?: boolean;
  rightIcon?: boolean;
  button?: boolean;
  extra?: boolean;
}

export interface FigmaDataType {
  type: string; // or other...
  renderPath?: string; // 
  defineProps?: boolean; // defineProps
  defineEmits?: boolean; // defineEmits
  formItems: FormItem[]; // 
}

// 
interface RequestData {
  figmaData: FigmaDataType;
}

// bodyParser  ctx.request.body ,  Request 
declare module 'koa' {
  interface Request {
    body: RequestData;
  }
}

interface ResponseData {
  status: 'success' | 'error';
  data?: any;
  message?: string;
}

export const openServer = (
  PORT: number = 2023,
  callback: (reqData: RequestData['figmaData']) => void,
) => {
  const app = new Koa();
  const router = new Router();

  app.use(cors());
  app.use(bodyParser());

  // 
  app.use(async (ctx: Context, next: Next) => {
    try {
      await next();
    } catch (err: any) {
      ctx.status = err.status || 500;
      ctx.body = {
        status: 'error',
        message: err.message || 'Internal Server Error',
      };
      ctx.app.emit('error', err, ctx); // 
    }
  });

  // POST /submitFigmaData
  router.post('/submitFigmaData', async (ctx: Context) => {
    const requestBody: RequestData = ctx.request.body;

    if (!requestBody.figmaData) {
      throw new Error('figmaData is missing in the request body');
    }
    const responseData: ResponseData = {
      status: 'success',
      data: 'figma',
    };
    ctx.body = responseData;
    // figma
    callback(requestBody.figmaData);
    // 
    server.close();
  });

  app.use(router.routes()).use(router.allowedMethods());

  const server = app.listen(PORT, () => {
    console.log(`Server running on http://localhost:${PORT}`);
  });

  // 
  app.on('error', (err: any) => {
    console.error('Server error', err);
  });
};
